<template>
  <div class="videocontent" v-if="contentitem">
    <div class="content" :class="{ active: $store.state.isactive }">
      <div class="play">
        <div class="back" @click="$router.go(-1)">
          <i class="fa fa-angle-left" aria-hidden="true"></i>
        </div>
        <div class="bg" :class="{ active: bgshows }" @click="showbg"></div>
        <div class="btn" v-if="showbtn">
          <i
            class="fa fa-pause"
            aria-hidden="true"
            v-if="!playstate"
            @click="playpuse"
          ></i>
          <i class="fa fa-play" aria-hidden="true" v-else @click="playpuse"></i>
        </div>
        <video class="video" ref="video" autoplay>
          <source
            :src="contentitem.data.data.content_video[0].progressive[0].url"
            type="video/mp4"
          />
        </video>
        <van-slider v-model="value" @change="onChange" bar-height="2px" />
      </div>
      <div class="headercontent">
        <h1>{{ contentitem.data.data.title }}</h1>
        <h6>
          {{ contentitem.data.data.cate[0] }}<span>/</span
          >{{ getmin(contentitem.data.data.duration) }}
        </h6>
        <div class="contenttop">
          <p>{{ contentitem.data.data.intro }}</p>
          <h5>
            <span @click="goReadeMore">
              阅读全文<i class="fa fa-long-arrow-right" aria-hidden="true"></i
            ></span>
          </h5>
        </div>
        <ul class="correlation" v-if="contentitem.data.data.relate_video">
          <li
            class="icon"
            v-for="(item, index) in contentitem.data.data.relate_video[0].list"
            :key="index"
            @click="gocontenttody(item)"
          >
            <div class="message"><img :src="item.image" alt="" /></div>
            <p>{{ getmin(item.duration) }}</p>
            <h4>{{ item.title }}</h4>
          </li>
        </ul>

        <div class="comment" @touchstart="touchstarts" @touchend="touchends">
          <h3 v-if="contentitem.data.data.comment.list">
            {{ contentitem.data.data.comment.list.length }}条评论
          </h3>
          <ul class="commentcontent">
            <li
              v-for="(item, index) in contentitem.data.data.comment.list"
              :key="index"
            >
              <div class="firstcomment">
                <div class="left ">
                  <img :src="item.userinfo.avatar" alt="" />
                </div>
                <div class="conten">
                  <div class="name">{{ item.userinfo.username }}</div>
                  <div class="time">{{ gethous(item.addtime) }}前</div>
                  <div class="cont">{{ item.content }}</div>
                </div>
                <div class="right"><van-icon name="good-job-o" />0</div>
              </div>
              <div class="comment" v-if="item.subcomment">
                <div
                  class="comments"
                  v-for="(items, index) in item.subcomment"
                  :key="index"
                >
                  <div class="left ">
                    <img :src="items.userinfo.avatar" alt="" />
                  </div>
                  <div class="conten">
                    <div class="name">{{ items.userinfo.username }}</div>
                    <div class="time">{{ gethous(items.addtime) }}前</div>
                    <div class="cont">
                      回复{{ item.userinfo.username }}：{{ items.content }}
                    </div>
                  </div>
                  <div class="right"><van-icon name="good-job-o" />0</div>
                </div>
              </div>
            </li>
          </ul>
        </div>
        <van-list
          v-model="loading"
          :finished="finished"
          finished-text="没有更多了"
          error-text="没有啦！主人"
          :error.sync="error"
          @load="onLoad"
        >
          <template>
            <van-cell v-for="(listsitem, index) in daysitemslist" :key="index">
              <li v-for="(item, index) in listsitem" :key="index">
                <div class="left ">
                  <img :src="item.userinfo.avatar" alt="" />
                </div>
                <div class="conten">
                  <div class="name">{{ item.userinfo.username }}</div>
                  <div class="time">{{ gethous(item.addtime) }}小时前</div>
                  <div class="cont">{{ item.content }}</div>
                </div>
                <div class="right"><van-icon name="good-job-o" />0</div>
              </li>
            </van-cell>
          </template>
        </van-list>
      </div>
      <div class="collect">
        <div class="input"><van-icon name="edit" color="#080808" /></div>
        <div class="collectitem">
          <van-icon name="like-o" v-if="!userislike" @click="Collect" />
          <van-icon name="like" color="#E22018" v-else @click="Collect" />
          <h4>{{ contentitem.data.data.count_like }}</h4>
        </div>
        <div class="uploading">
          <van-icon name="upgrade" />
          <h4>{{ contentitem.data.data.count_share }}</h4>
        </div>
        <div class="downlodow">
          <van-icon name="down" />
          <h4>缓存</h4>
        </div>
      </div>
    </div>
    <div
      class="redmore"
      :class="{ active: isactive }"
      @touchstart="touchredmore"
      @touchend="touchendsredmore"
    >
      <div class="back" @click="isactive = false">
        <i class="fa fa-angle-left" aria-hidden="true"></i>
      </div>
      <h1>{{ contentitem.data.data.title }}</h1>
      <h3>{{ contentitem.data.data.format_content[0].content }}</h3>
      <ul>
        <li
          v-for="(item, index) in contentitem.data.data.format_content"
          :key="index"
        >
          <div v-if="item.content">
            <!-- <a href=""></a> -->
            <!-- {{ GetChinese(item.content) }} -->
            {{ item.content }}
          </div>

          <!-- {{ contentitem.data.data.format_content[2].content }} -->
        </li>
      </ul>
    </div>

    <router-view></router-view>
  </div>
</template>

<script>
import Vue from "vue";
export default {
  name: "FindItemContent",
  data() {
    return {
      value: 0,
      contentitem: null,
      list: [],
      lists: [],
      loading: false,
      error: false,
      iserror: false,
      finished: false,
      daysitemslist: [],
      ispush: true,
      isdiffent: false,
      isurl: false,
      startPagx: null,
      playtime: 0,
      audototalTime: 0,
      playstate: false,
      bgshows: false,
      showbtn: false,
      isactive: false,
      url: [],
      userislike: false,
      islike: false,
      detectionhistoryisdiffent: false,
      detectionhistoryisdiffents: false,
      indexi: null
    };
  },
  watch: {
    detectionhistoryisdiffents() {
      let user = JSON.parse(window.localStorage.getItem("user"));
      if (this.detectionhistoryisdiffents) {
        window.console.log("没有相同");
        user.data.playhistory.unshift(this.$store.state.likeitem);
        window.localStorage.setItem(
          "user",
          JSON.stringify({
            tel: user.tel,
            password: user.password,
            data: {
              like: user.data.like,
              playhistory: user.data.playhistory
            }
          })
        );
      }
    },
    detectionhistoryisdiffent() {
      let user = JSON.parse(window.localStorage.getItem("user"));
      if (this.detectionhistoryisdiffent) {
        window.console.log("有相同");
        user.data.playhistory.splice(this.indexi, 1);
        user.data.playhistory.unshift(this.$store.state.likeitem);
        window.localStorage.setItem(
          "user",
          JSON.stringify({
            tel: user.tel,
            password: user.password,
            data: {
              like: user.data.like,
              playhistory: user.data.playhistory
            }
          })
        );
      }
    },
    // value() {
    //   this.$refs.video.currentTime =
    //     (this.value / 100) * this.$refs.video.duration;
    // },
    contentitem() {
      if (this.contentitem) {
        this.$store.commit("submitcontentitem", this.contentitem);
        // this.contentitem.data.data.format_content.forEach(e => {
        //   // this.url.push(this.httpString(e.content));
        //   // window.console.log(e.content);
        //   // window.console.log(String(e.content));
        //   let n = String(e.content);

        //   // window.console.log(n.indexOf("content="));
        //   // window.console.log(n.indexOf("]"));
        //   // window.console.log(this.httpString(n));
        //   // let m =
        //   // window.console.log()
        // });
      }
    }
  },
  methods: {
    //组件创建时判断是否点亮桃心
    isLightenXin() {
      let user = JSON.parse(window.localStorage.getItem("user"));
      if (user) {
        if (user.data.like[0]) {
          user.data.like.forEach(e => {
            // window.console.log(e.postid);
            if (e.postid === this.$store.state.likeitem.postid) {
              this.userislike = true;
              return;
            }
          });
        }
      }
    },
    //增加收藏
    Collect() {
      let user = JSON.parse(window.localStorage.getItem("user"));
      if (user) {
        this.userislike = !this.userislike;
        if (this.userislike) {
          if (user.data.like[0]) {
            user.data.like.forEach(e => {
              if (e.postid === this.$store.state.likeitem.postid) {
                return;
              } else {
                user.data.like.unshift(this.$store.state.likeitem);
                window.localStorage.setItem(
                  "user",
                  JSON.stringify({
                    tel: user.tel,
                    password: user.password,
                    data: {
                      like: user.data.like,
                      playhistory: user.data.playhistory
                    }
                  })
                );
              }
            });
          } else {
            user.data.like.unshift(this.$store.state.likeitem);
            window.localStorage.setItem(
              "user",
              JSON.stringify({
                tel: user.tel,
                password: user.password,
                data: {
                  like: user.data.like,
                  playhistory: user.data.playhistory
                }
              })
            );
          }
        } else {
          user.data.like.forEach((e, i) => {
            if (e.postid === this.$store.state.likeitem.postid) {
              user.data.like.splice(i, 1);
              window.localStorage.setItem(
                "user",
                JSON.stringify({
                  tel: user.tel,
                  password: user.password,
                  data: {
                    like: user.data.like,
                    playhistory: user.data.playhistory
                  }
                })
              );
            }
          });
        }
      } else {
        this.$router.push("/portrait");
      }
    },
    //存浏览过的历史记录
    SaveHistory() {
      let user = JSON.parse(window.localStorage.getItem("user"));
      let b = true;
      let c = true;
      if (user) {
        if (user.data.playhistory[0]) {
          user.data.playhistory.forEach((e, i) => {
            if (e.postid === this.$store.state.likeitem.postid) {
              window.console.log(
                e.postid === this.$store.state.likeitem.postid
              );
              if (b) {
                window.console.log("有相同", i);
                this.detectionhistoryisdiffent = true;
                this.indexi = i;
                b = false;
                c = false;
              }
              return;

              //
              //   // user.data.playhistory.splice(i, 1);
              //   // user.data.playhistory.unshift(this.$store.state.likeitem);
              //   // window.localStorage.setItem(
              //   //   "user",
              //   //   JSON.stringify({
              //   //     tel: user.tel,
              //   //     password: user.password,
              //   //     data: {
              //   //       like: user.data.like,
              //   //       playhistory: user.data.playhistory
              //   //     }
              //   //   })
              //   // );
              // } else {
              //   window.console.log("没有相同", i);
              //   this.detectionhistoryisdiffents = true;
              //   // user.data.playhistory.unshift(this.$store.state.likeitem);
              //   // window.localStorage.setItem(
              //   //   "user",
              //   //   JSON.stringify({
              //   //     tel: user.tel,
              //   //     password: user.password,
              //   //     data: {
              //   //       like: user.data.like,
              //   //       playhistory: user.data.playhistory
              //   //     }
              //   //   })
              //   // );
            } else {
              setTimeout(() => {
                if (c) {
                  window.console.log("没有相同谢谢谢谢", i);
                  b = false;
                  c = false;
                  this.detectionhistoryisdiffents = true;
                }
              }, 0);
            }
          });
        } else {
          user.data.playhistory.unshift(this.$store.state.likeitem);
          window.localStorage.setItem(
            "user",
            JSON.stringify({
              tel: user.tel,
              password: user.password,
              data: { like: user.data.like, playhistory: user.data.playhistory }
            })
          );
          window.console.log("已经有了用户但是没有历史");
        }
      }
    },
    gocontenttody(item) {
      // window.console.log(item.postid, item);
      this.$store.commit("submitIsactive", true);
      this.$router.push({
        path: "/finditemcontents",
        query: { id: item.postid }
      });
    },
    httpString(s) {
      // var reg = /(http:\/\/|https:\/\/)((\w|=|\?|\.|\/|&|-)+)/g;
      // var reg = /^(https?:\/\/)?([\da-z\.-]+)\.([a-z\.]{2,6})([\/\w \.-]*)*\/?$/;
      // var reg=/(http(s)?\:\/\/)?(www\.)?(\w+\:\d+)?(\/\w+)+\.(swf|gif|jpg|bmp|jpeg)/gi;
      // var reg=/(http(s)?\:\/\/)?(www\.)?(\w+\:\d+)?(\/\w+)+\.(swf|gif|jpg|bmp|jpeg)/gi;
      var reg = /(https?|http|ftp|file):\/\/[-A-Za-z0-9+&@#/%?=~_|!:,.;]+[-A-Za-z0-9+&@#/%=~_|]/g;
      // var reg = /^((ht|f)tps?):\/\/[\w\-]+(\.[\w\-]+)+([\w\-\.,@?^=%&:\/~\+#]*[\w\-\@?^=%&\/~\+#])?$/;
      //v = v.replace(reg, "<a href='$1$2'>$1$2</a>"); //这里的reg就是上面的正则表达式
      //s = s.replace(reg, "$1$2"); //这里的reg就是上面的正则表达式
      s = s.match(reg);
      window.console.log(s);
      return s;
    },
    //过滤出中文
    GetChinese(strValue) {
      if (strValue != null && strValue != "") {
        let regs = /\[(.+?)\]/g;
        let x = strValue.match(regs);
        if (x) {
          let n = x.join("");
          // window.console.log(n);
          if (n) {
            let reg = /[\u4e00-\u9fa5]/g;
            let c = n.match(reg).join("");
            // window.console.log(strValue.replace(n, "mcasbc"));
            // window.console.log(c);
            let y = strValue.replace(n, c);
            return y;
          }
        }
      } else return "";
    },
    playpuse() {
      window.console.log("播放");
      this.playVideo();
      this.playstate = !this.playstate;
      if (this.playstate == false) {
        this.bgshows = true;
      }
      // if (this.$refs.video.paused) {
      //   setTimeout(() => {
      //     this.bgshows = false;
      //     this.showbtn = false;
      //   }, 3000);
      // }
      // if (!this.$refs.video.paused && !this.playstate) {
      //   setTimeout(() => {
      //     this.bgshows = false;
      //     this.showbtn = false;
      //   }, 3000);
      // }
    },
    showbg() {
      window.console.log("beij");
      this.showbtn = true;
      this.bgshows = true;
      if (!this.$refs.video.paused && !this.playstate) {
        setTimeout(() => {
          this.bgshows = false;
          this.showbtn = false;
        }, 3000);
      }
    },
    play() {
      let _that = this;
      this.$refs.video.ontimeupdate = function() {
        // _that.Singerpanduan();
        // window.console.log(this.currentTime);
        _that.playtime = this.currentTime;
        _that.audototalTime = this.duration;
        _that.value = (this.currentTime / this.duration) * 100;
      };
    },
    playVideo() {
      if (this.$refs.video.paused && this.playstate) {
        this.$refs.video.play();
        setTimeout(() => {
          this.bgshows = false;
          this.showbtn = false;
        }, 3000);
      } else {
        this.$refs.video.pause();
        this.bgshows = true;
        this.showbtn = true;
        // this.bgshows = true;
        // this.playstate = false;
      }
      this.play();
    },
    touchstarts(event) {
      this.startPagx = event.touches[0].pageX;
      // window.console.log(this.startPagx);
    },
    touchends(event) {
      let x = event.changedTouches[0].pageX;
      if (x - this.startPagx >= 100) {
        this.$router.go(-1);
      }
    },
    touchredmore(event) {
      this.startPagx = event.touches[0].pageX;
      // window.console.log(this.startPagx);
    },
    touchendsredmore(event) {
      let x = event.changedTouches[0].pageX;
      if (x - this.startPagx >= 100) {
        // this.$router.go(-1);
        this.isactive = false;
      }
    },
    //阅读更多
    goReadeMore() {
      this.isactive = true;
      // window.console.log("xxx");
      // this.$router.push({ path: "/finditemcontent/redmore" });
    },
    getfindheaderdata(p) {
      if (this.$store.state.isgetdata) {
        this.axios
          .get("http://api.kele8.cn/agent/" + p + "")
          .then(reponse => {
            if (this.ispush) {
              this.lists.push(reponse.data.data.next_page_url_full);
              this.daysitemslist.push(reponse.data.data.list);
              this.finished = true;
              this.ispush = false;
            } else {
              this.daysitemslist.forEach(element => {
                if (element[0].content !== reponse.data.data.list[0].content) {
                  this.isdiffent = false;
                } else {
                  this.isdiffent = true;
                }
              });
              this.lists.forEach(element => {
                if (element !== reponse.data.data.next_page_url_full) {
                  this.isurl = false;
                } else {
                  this.isurl = true;
                }
              });
              if (!this.isdiffent && reponse.data.data && !this.isurl) {
                this.lists.push(reponse.data.data.next_page_url_full);
                this.daysitemslist.push(reponse.data.data.list);
                this.finished = true;
              }
            }
          })
          .catch(() => {
            this.error = true;
            this.iserror = true;
          });
      }
    },
    onLoad() {
      if (this.iserror) {
        this.loadfinish = true;
      }
      // 异步更新数据
      setTimeout(() => {
        // for (let i = 0; i < this.lists.length; i++) {
        //   this.list.push(this.list.length + 1);
        // }
        // 加载状态结束
        this.loading = false;
        // 数据全部加载完成
        if (this.list.length <= this.lists.length) {
          this.finished = true;
        }
        this.finished = false;
      }, 500);
      this.getfindheaderdata(this.lists[this.lists.length - 1]);
    },
    onChange(value) {
      this.$refs.video.currentTime = (value / 100) * this.$refs.video.duration;
      // window.console.log(value);
    },
    getmin(i) {
      let time = i;
      let n = Math.floor(time / 60);
      let s = Math.floor(time % 60);
      if (n < 10) {
        n = "0" + n;
      }
      if (s < 10) {
        // window.console.log("0" + s);
        s = "0" + s;
      }
      let m = n + "'" + s + '"';

      return m;
    },
    gethous(i) {
      let t = new Date().getTime() / 1000 - i;
      let n = Math.round(t / 60 / 60);
      let s = n + "小时";
      if (n >= 24 && n < 720) {
        s = Math.round(n / 24) + "天";
      }
      if (n >= 720 && n < 8760) {
        s = Math.round(n / 720) + "个月";
      }
      if (n >= 8760) {
        s = Math.round(n / 8760) + "年";
      }
      return s;
    }
  },
  beforeRouteEnter(to, from, next) {
    Vue.axios
      .get(
        "https://api.kele8.cn/agent/https://app.vmovier.com/apiv3/post/view",
        {
          params: {
            postid: to.query.id
          }
        }
      )
      .then(function(response) {
        next(vm => {
          vm.contentitem = response;
          // window.console.log(response, "xxxxxxx");
          vm.lists.push(response.data.data.comment.next_page_url_full);
        });
      });
  },
  updated() {
    // window.console.log("创建finditemcontent");
  },
  created() {
    this.SaveHistory();
    this.isLightenXin();
  }
};
</script>

<style lang="less">
.videocontent {
  width: 100%;
  position: relative;
  .van-cell {
    padding: 0;
  }
  height: 100vh;
  overflow: hidden;
  .content {
    transition: all 0.5s;
    position: absolute;
    top: 100vh;
    display: flex;
    flex-direction: column;
    overflow: hidden;
    .play {
      display: flex;
      justify-content: center;
      position: relative;
      align-items: center;
      // padding: 1px 1px 0px 1px;
      width: 100%;
      .bg {
        transition: all 0.2s;
        position: absolute;
        left: 0;
        top: 0;
        width: 100%;
        height: 100%;
        background: none;
        z-index: 3;
        &.active {
          background: rgba(0, 0, 0, 0.3);
          transition: all 0.2s;
        }
      }

      .btn {
        position: absolute;
        left: 0;
        top: 50%;
        margin-top: -20px;
        width: 100%;
        color: white;
        text-align: center;
        font-size: 30px;
        z-index: 3;
      }
      .back {
        position: absolute;
        color: white;
        left: 20px;
        top: 15px;
        font-size: 22px;
        z-index: 5;
      }
      .van-slider {
        padding: 0px 1px;
        position: absolute;
        left: 0;
        bottom: 0px;
        height: 3px;
        background: rgb(77, 74, 74);
        z-index: 6;
        // margin-left: 0.5%;
        width: 99.8%;
        .van-slider__button-wrapper {
          width: 5px;
          height: 5px;
        }
        .van-slider__button {
          width: 5px;
          height: 5px;
          background: lightseagreen;
        }
        .van-slider__bar {
          -webkit-transition: width 0s;
          transition: width 0s;
        }
      }
    }
    .headercontent {
      flex: 1;
      overflow: hidden;
      overflow-y: scroll;
    }
    .collect {
      width: 100%;
      height: 6.5%;
      border-top: 1px solid rgb(196, 187, 187);
      display: flex;
      .input {
        width: 40%;
        margin: 7px 10px 7px 10px;
        height: 70%;
        border: 1px solid rgb(196, 187, 187);
        border-radius: 15px;
        .van-icon {
          padding: 4px 8px;
          font-size: 20px;
          font-weight: 600;
        }
      }
      .collectitem {
        text-align: center;
        flex: 1;
        padding: 5px 0;
        h4 {
          font-size: 10px;
        }
